@use "global" as *;

.modal {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(32, 32, 32, 0.8);
  justify-content: center;
  align-items: center;
  z-index: 10000;
}

