.modal__image {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  //画像の中心点がモーダルの中央に一致
  object-fit: contain; //アスペクト比を保ち、指定された範囲内に収める
  width: 80%;
  height: 80%;
  // max-width: rem(2000);
  max-width: rem(920);
  max-height: 80vh; // 高さの最大値を設定
}
